@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');
.main{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: #D9D9D9;
  font-family: "Inter", sans-serif;
}
.button-save:focus{
  outline: 2px solid rgba(32, 32, 32, 0.836);
}

.text-field:focus{
  outline: 2px solid rgba(22, 159, 223, 0.836);
}
.text-field{
    font-size: 14px;
}

.table-loading{
    background-color: rgba(112, 110, 110, 0.178);
    position: absolute;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;

  }
  .table-loading .loader-container{
    width: 70px;
    height: 70px;
    background-color: transparent;
    position: relative;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
  }


  .loader {
    position: relative;
    width:  48px;
    height: 48px;
    background: #333232;
    transform: rotateX(65deg) rotate(45deg);
    /* transform: perspective(200px) rotateX(65deg) rotate(45deg);  */
    color: #fff;
    animation: layers1 1s linear infinite alternate;
  }
  .loader:after {
    content: '';
    position: absolute;
    inset: 0;
    background: rgba(255, 255, 255, 0.7);
    animation: layerTr 1s linear infinite alternate;
  }

  @keyframes layers1 {
    0% { box-shadow: 0px 0px 0 0px  }
   90% , 100% { box-shadow: 20px 20px 0 -4px  }
  }
  @keyframes layerTr {
    0% { transform:  translate(0, 0) scale(1) }
    100% {  transform: translate(-25px, -25px) scale(1) }
  }
      
  
.ripple-button {
  position: relative;
  overflow: hidden;
  outline: none;
  cursor: pointer;
}

.ripple-button::before {
  content: "";
  position: absolute;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  transform: scale(0);
  width: 100px;
  height: 100px;
  opacity: 0;
  pointer-events: none;
  transition: transform 0.5s, opacity 1s;
}

.ripple-button:active::before {
  opacity: 1;
  transform: scale(4);
}

.ripple {
  position: absolute;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.5);
  transform: scale(0);
  animation: ripple-animation 0.6s linear;
  pointer-events: none;
}

@keyframes ripple-animation {
  to {
    transform: scale(4);
    opacity: 0;
  }
}

.text-field-error{
    margin-top: 3px;
    font-size: 12px;
    padding: 0;
    margin: 0;
    color: rgb(190, 14, 14);
}